body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {

    background: rgb(180, 177, 198);
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    border-radius: 6px; 
  }
body {
    background: rgb(180, 177, 198);
}

nav {
    background: rgb(41, 3, 79);
    color: rgb(238, 238, 224);
}

.jumbotron {
    background: rgba(213, 187, 233, 0.6);
    color: rgb(68, 59, 101);
}
.card {
    background: rgb(41, 3, 79);
    border-radius: 2px;
    height: 150px;
    margin: 1rem;
    position: relative;
    width: 150px;
    box-shadow: 0 3px 6px rgb(0, 0, 0), 0 3px 6px rgb(0, 0, 0);
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
  }
  .card > .img-container > img {
    width: 90%;
    margin-top: 5px;
  }

  
  .img-container {
    text-align: center;
    margin-top: auto;
    margin-bottom: auto;
  }
